<template>
  <div id="preview-contract" class="preview">
    <iframe id="contract" :src="path" width="100%" height="90%">
      您的浏览器不支持iframe，请下载文件：<a :href="path">{{ filename }}</a>
    </iframe>
    <el-row>
      <el-col>
        <center>
          <br>
          <el-button type="primary" size="medium" @click="close">关 闭</el-button>
        </center>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  name: 'previewContract',
  data () {
    return {
      path: '',
      filename: ''
    }
  },
  created () {
    this.path = this.$route.query.path
    let len = this.path.split('/').length
    this.filename = this.path.split('/')[len - 1]
  },
  methods: {
    close () {
      this.$router.go(-1)
    }
  }
}
</script>
<style scoped>
.preview {
  /* overflow: hidden; */
  width: 100%;
  height: 95%;
  padding-top: 10px;
}
</style>



